import React from "react";
import { graphql } from "gatsby";
import Img from "gatsby-image";
import Layout from "./Layout";
import headset from "../Images/headset.png";
import tel from "../Images/tel.png";
import location from "../Images/location.png";
import mail from "../Images/mail.png";
import mobile from "../Images/mobile.png";
import { SEO } from "theme-seo";
import { GetBackendUrl } from "theme-library";

const ContactIcon = ({ iconName }) => {
  let icon = headset;
  if (iconName === "tel") icon = tel;
  else if (iconName === "location") icon = location;
  else if (iconName === "mail") icon = mail;
  else if (iconName === "mobile") icon = mobile;

  return (
    <img src={icon} alt={iconName} className="inline" style={{ height: "25px", width: "25px" }} />
  );
};
const ContactUs = ({ pageContext, data }) => {
  let parseServerUrl = pageContext.parseServerUrl;
  const backendUrl = GetBackendUrl();
  if (backendUrl !== null) parseServerUrl = backendUrl;

  const defaultButtonState = { disabled: true, buttonStyle: "cursor-not-allowed" };
  const [buttonState, setButtonState] = React.useState(defaultButtonState);

  let initialFeedback = { name: "", phone: "", message: "" };
  const [feedback, setFeedback] = React.useState(initialFeedback);

  function InputOnChange(event) {
    let element = event.target;
    if (element.name === "phone") {
      if (element.value !== "" && !/^[0-9\b]+$/.test(element.value)) {
        return;
      }
    }

    let feedbackClone = { ...feedback };
    feedbackClone[event.target.name] = element.value;
    setFeedback(feedbackClone);

    let buttonStateClone = { ...buttonState };
    buttonStateClone.disabled = !(
      feedbackClone.name &&
      feedbackClone.phone &&
      feedbackClone.message
    );
    buttonStateClone.buttonStyle = buttonStateClone.disabled
      ? "cursor-not-allowed"
      : "opacity-100 hover:bg-green-600";
    setButtonState(buttonStateClone);
  }

  function Send() {
    const feedbackPost = {
      Name: feedback.name,
      PhoneNumber: feedback.phone,
      Message: feedback.message,
    };

    fetch(parseServerUrl + "parse/classes/Feedback", {
      method: "POST",
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(feedbackPost),
    }).then(function (response) {
      let promise = response.json();
      return promise;
    });
    Clear("input");
    Clear("textarea");

    setButtonState(defaultButtonState);
    setFeedback(initialFeedback);
  }

  function Clear(tagName) {
    let elements = document.getElementsByTagName(tagName);
    for (let i = 0; i < elements.length; i++) {
      elements[i].value = "";
    }
  }

  return (
    <Layout>
      <SEO title={pageContext.title} />
      <Img fluid={data.allFile.nodes[0].childImageSharp.fluid} />
      <div className="w-10/12 lg:w-3/5 mx-auto">
        <div className="my-10">
          <p className="mx-auto my-4 text-2xl">
            شما می‌توانید از طریق راه‌های ارتباطی زیر با ما در تماس باشید.
          </p>
        </div>
        <div
          className="flex w-full flex-wrap justify-between"
          data-aos="zoom-in"
          data-aos-duration="900"
        >
          {pageContext.contacts.map((contact, i) => (
            <div key={contact.name} className={"w-full lg:whitespace-no-wrap lg:" + (i % 2 ? "w-1/3" : "w-2/3")}>
              <ContactIcon iconName={contact.icon} />
              <span className="mx-2 font-bold">{contact.name}:</span>
              <span className="text-sm">{contact.value}</span>
            </div>
          ))}
        </div>
        <div>
          <div className="my-10">
            <div>
              <p className="text-center mx-auto my-2 text-3xl font-bold text-teal-600 ">
                تماس با ما
              </p>
            </div>
          </div>
          <form>
            <div>
              <label className="block text-gray-700 text-sm font-bold my-2">
                شماره تماس
                <span className="text-red-800 m-1">*</span>
              </label>
              <input
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                type="text"
                name="phone"
                onChange={InputOnChange}
              />
            </div>
            <div>
              <label className="block text-gray-700 text-sm font-bold my-2">
                نام و نام خانوادگی
                <span className="text-red-800 m-1">*</span>
              </label>
              <input
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                type="text"
                name={"name"}
                onChange={InputOnChange}
              />
            </div>
            <div>
              <label className="block text-gray-700 text-sm font-bold my-2">
                پیام شما
                <span className="text-red-800 m-1">*</span>
              </label>
              <textarea
                className="h-40 shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                type="text"
                name={"message"}
                onChange={InputOnChange}
              />
            </div>
            <button
              type={"button"}
              onClick={Send}
              disabled={buttonState.disabled}
              className={
                "m-2 bg-teal-600 text-white font-bold opacity-50  py-2 px-4 rounded-lg w-32 " +
                buttonState.buttonStyle
              }
            >
              ارسال
            </button>
          </form>
        </div>
      </div>
    </Layout>
  );
};

export default ContactUs;

export const query = graphql`
  {
    allFile(filter: { name: { eq: "contactUs" } }) {
      nodes {
        name
        childImageSharp {
          fluid(maxWidth: 2500) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  }
`;
